.contact-info {
  padding-top: 50px;
  background-color: #fff;
  margin-top: 104px;
}

.contacts-info_wrap {
  display: flex;
  justify-content: space-between;
  padding-bottom: 80px;
  border-bottom: 2px solid #E9E9E9;
}

.contacts-info_wrap .left {
  width: 57%;
}

.contacts-info_wrap .right {
  width: 32%;
}

.contact-titles {
  padding-bottom: 40px;
  margin-bottom: 40px;
  border-bottom: 2px solid #E9E9E9;
}

.contact-titles h1 {
  font-size: 36px;
  margin-bottom: 8px;
}

.contact-titles .contact-undertitle {
  font-size: 17px;
  line-height: 26px;
}

.contacts-info_titles h6 {
  font-size: 22px;
  font-weight: 500;
  line-height: 30px;
  margin-bottom: 8px;
}

.contacts-info_titles p {
  font-size: 15px;
  line-height: 22px;
  margin-bottom: 25px;
}

.contacts-info_content {
  border: 2px solid #E9E9E9;
  border-radius: 4px;
  overflow: hidden;
  padding: 30px 75px;
  margin-bottom: 40px;
}

.contacts-info_content a {
  display: block;
  font-size: 17px;
  line-height: 30px;
  margin-bottom: 4px;
  color: #1F283C;
}

.contacts-info_content p {
  font-size: 15px;
  line-height: 20px;
  color: #23A8CD;
}

.contacts-info_content .email {
  margin-bottom: 30px;
  position: relative;
}

.contacts-info_content .email:before {
  display: block;
  content: '';
  width: 32px;
  height: 32px;
  background: transparent url("../img/email-icon.svg") center no-repeat;
  background-size: contain;
  position: absolute;
  left: -45px;
  top: 2px;
}

.contacts-info_content .phone {
  margin-bottom: 30px;
  position: relative;
}

.contacts-info_content .phone:before {
  display: block;
  content: '';
  width: 32px;
  height: 32px;
  background: transparent url("../img/phone-icon.svg") center no-repeat;
  background-size: contain;
  position: absolute;
  left: -45px;
  top: 5px;
}

.contacts-info_content .phone p {
  color: #797C84;
}

.contacts-info_content .adress {
  position: relative;
  margin-bottom: 30px;
}

.contacts-info_content .adress:last-child {
  margin-bottom: 0;
}

.contacts-info_content .adress:before {
  display: block;
  content: '';
  width: 32px;
  height: 32px;
  background: transparent url("../img/location-icon.svg") center no-repeat;
  background-size: contain;
  position: absolute;
  left: -45px;
  top: 5px;
}

.contact-social {
  display: flex;
  margin-bottom: 40px;
  padding-bottom: 40px;
  border-bottom: 2px solid #E9E9E9;
}

.contact-social a {
  display: block;
  width: 42px;
  height: 42px;
  border: 2px solid #E9E9E9;
  border-radius: 4px;
  overflow: hidden;
  margin-right: 8px;
}

.contact-social a:last-child {
  margin-right: 0;
}

.contact-social .facebook {
  background: transparent url("../img/Facebook.svg") center no-repeat;
  background-size: 90%;
}

.contact-social .trip-advisor {
  background: transparent url("../img/TripAdvisor.svg") center no-repeat;
  background-size: 90%;
}

.contact-social .twitter {
  background: transparent url("../img/Twitter.svg") center no-repeat;
  background-size: 90%;
}

.contact-social .instagram {
  background: transparent url("../img/Instagram.svg") center no-repeat;
  background-size: 90%;
}

.contact-social .linkedin {
  background: transparent url("../img/LinkedIn.svg") center no-repeat;
  background-size: 90%;
}

.contact-social .youtube {
  background: transparent url("../img/YouTube.svg") center no-repeat;
  background-size: 90%;
}

.contact-social .watsap {
  background: transparent url("../img/WhatsApp.svg") center no-repeat;
  background-size: 90%;
}

.contacts-additional h4 {
  font-size: 22px;
  font-weight: 500;
  line-height: 30px;
  margin-bottom: 25px;
}

.contacts-additional .contacts-additional_wrap {
  display: flex;
  justify-content: space-between;
}

.contacts-additional .contacts-additional_wrap .column {
  width: 48%;
}

.contacts-additional .contacts-additional_wrap .column h6 {
  font-size: 18px;
  font-weight: 500;
  line-height: 28px;
  margin-bottom: 4px;
}

.contacts-additional .contacts-additional_wrap .column p {
  font-size: 15px;
  line-height: 22px;
}

@media screen and (max-width: 1200px) {
  .contacts-info_wrap .right {
    width: 37%;
  }
}

@media screen and (max-width: 900px) {
  .contacts-info_wrap {
    flex-direction: column-reverse;
    padding-top: 120px;
    position: relative;
  }
  .contacts-info_wrap .left {
    width: 100%;
  }
  .contacts-info_wrap .right {
    width: 100%;
    margin-bottom: 40px;
  }
  .contact-titles {
    position: absolute;
    top: 0;
    left: 0;
    border-bottom: 0;
  }
}

@media screen and (max-width: 500px) {
  .contact-info {
    padding-top: 30px;
    margin-top: 79px;
  }
  .contacts-info_wrap {
    padding-bottom: 30px;
  }
  .contacts-info_wrap .right {
    margin-bottom: 30px;
    padding-bottom: 30px;
    border-bottom: 2px solid #E9E9E9;
  }
  .contact-titles h1 {
    font-size: 26px;
    line-height: 36px;
  }
  .contact-titles p {
    font-size: 16px;
    line-height: 23px;
    margin-bottom: 25px;
  }
  .contacts-info_titles h6 {
    font-size: 21px;
    line-height: 30px;
    margin-bottom: 4px;
  }
  .contacts-info_titles p {
    font-size: 14px;
    line-height: 20px;
    margin-bottom: 23px;
  }
  .contacts-info_content {
    padding: 28px 15px 28px 65px;
    margin-bottom: 23px;
  }
  .contacts-info_content a {
    font-size: 16px;
    line-height: 23px;
  }
  .contacts-info_content p {
    font-size: 14px;
    line-height: 20px;
  }
  .contacts-info_content .email:before, .contacts-info_content .phone:before, .contacts-info_content .adress:before {
    top: 0;
  }
  .contact-social {
    padding-bottom: 30px;
    margin-bottom: 30px;
  }
  .contact-social a {
    width: 40px;
    height: 40px;
  }
  .contacts-additional h4 {
    font-size: 21px;
    line-height: 30px;
    margin-bottom: 15px;
  }
  .contacts-additional .contacts-additional_wrap {
    flex-wrap: wrap;
  }
  .contacts-additional .contacts-additional_wrap .column {
    width: 100%;
    margin-bottom: 15px;
  }
  .contacts-additional .contacts-additional_wrap .column:last-child {
    margin-bottom: 0;
  }
  .contacts-additional .contacts-additional_wrap .column h6 {
    font-size: 17px;
    line-height: 23px;
    margin-bottom: 2px;
  }
  .contacts-additional .contacts-additional_wrap .column p {
    font-size: 14px;
    line-height: 20px;
  }
}

@media screen and (max-width: 350px) {
  .contacts-info_content {
    padding: 28px 15px 28px 55px;
  }
  .contacts-info_content a {
    font-size: 15px;
  }
  .contacts-info_content p {
    font-size: 13px;
  }
}

